<template>
    <div>
        <div class="panel d-flex align-items-center" @keydown="keyCodes($event)" tabindex="0">
            <div class="calc mx-auto"> 
                <div class="display">
                    <div class="formula ml-auto">
                        <div class="scrollbar">{{formula}}</div>
                        <div class="sum">{{total | currency}}</div>
                    </div>
                </div>
                <div class="button-group">
                    <div class="row m-0">
                        <div class="col-9">
                            <div class="row m-0">
                                <div class="col-4 numBtn" @click="append('7')" >7</div>
                                <div class="col-4 numBtn" @click="append('8')">8</div>
                                <div class="col-4 numBtn" @click="append('9')">9</div>
                                <div class="col-4 numBtn" @click="append('4')">4</div>
                                <div class="col-4 numBtn" @click="append('5')">5</div>
                                <div class="col-4 numBtn" @click="append('6')">6</div>
                                <div class="col-4 numBtn" @click="append('1')">1</div>
                                <div class="col-4 numBtn" @click="append('2')">2</div>
                                <div class="col-4 numBtn" @click="append('3')">3</div>
                                <div class="col-4 numBtn" @click="append('0')">0</div>
                                <div class="col-4 numBtn" @click="append('00')">00</div>
                                <div class="col-4 numBtn" @click="dot('.')">.</div>
                            </div>
                            
                        </div>
                        <div class="col-3 p-0">
                            <div class="row m-0">
                                <div class="col-12 operatorBtn" @click="divide">÷</div>
                                <div class="col-12 operatorBtn" @click="times">x</div>
                                <div class="col-12 operatorBtn" @click="add">+</div>
                                <div class="col-12 operatorBtn" @click="minus">-</div>
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="row m-0">
                                <div class="col-3 clear" @click="clearAll">AC</div>
                                <div class="col-3 back" @click="back">⌫</div>
                                <div class="col-6 sumBtn" @click="equal(true)">=</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css?family=Roboto');
@import '../../assets/scss/calc.scss';
</style>

<script>

export default {
    data(){
        return{
            formula: '0',
            total: '0',
            operator: null,
            previous: null,
            operatorClicked: true,
            isfirst: true,
            isequal: true,
        }
    },
    methods:{
        dot(value){
            const vm = this;
            if(vm.total.indexOf('.') === -1){
                vm.append('.');
            }
        },
        append(number){
            const vm = this;
            
            if(this.operatorClicked){
                vm.operatorClicked = false;
                vm.total = '';
            }
            vm.total = `${vm.total}${number}`;
        },
        setPrevious(){
            const vm = this;
            vm.previous = vm.total  ;  //按加後把他寫過去下一個
            vm.operatorClicked = true; 
            vm.isfirst = false;
        },
        divide(){
            const vm = this;
            if(vm.formula === '0'){
                vm.formula = '';
            }
            vm.formula = vm.formula + vm.total + ' / ';
            
            if(!vm.isfirst){
                vm.isequal = false;
                vm.equal();
            }
            vm.operator = (a,b) => a / b;
            vm.setPrevious();
        },
        times(){
            const vm = this;

            if(vm.formula === '0'){
                vm.formula = '';
            }
            vm.formula = vm.formula + vm.total + ' * ';

            if(!vm.isfirst){
                vm.isequal = false;
                vm.equal();
            }
            vm.operator = (a,b) => a * b;
            vm.setPrevious();
        },
        add(){
            const vm = this;

            if(vm.formula === '0'){
                vm.formula = '';
            }
            vm.formula = vm.formula + vm.total + ' + ';

            if(!vm.isfirst){
                vm.isequal = false;
                vm.equal();
            }
            vm.operator = (a,b) => a + b;
            vm.setPrevious();
        },
        minus(){
            const vm = this;

            if(vm.formula === '0'){
                vm.formula = '';
            }
            vm.formula = vm.formula + vm.total + ' - ';

            if(!vm.isfirst){
                vm.isequal = false;
                vm.equal();
            }
            vm.operator = (a,b) => a - b;
            vm.setPrevious();
        },
        back(){
            const vm = this;
            console.log(vm.total.length);
            vm.total = vm.total.slice(0,-1);
            if(vm.total.length === 0){
                vm.total = '0';
                vm.operatorClicked = true;
            }
        },
        equal(entryPoint){
            const vm = this;

            //防呆:首次進入,如果一開始就是按=,那operator沒有設定值+-*/,給預設值
            if(vm.isfirst){
                vm.operator = (a,b) => 0 + 0;
            }

            //防呆:如果後面連按= 而pervious沒有值,造成錯誤,把total寫入perious直接給倍數加總
            if(vm.previous === null){
                vm.previous = vm.total;
            }
            
            //這邊有兩個進入function的點,1.如果是由=進入的就把isequal開啟以提供下面判斷2.由+-*/四個點把開關為false,下面判斷自然就不會把pervios Null掉
            if(entryPoint){
                vm.isequal = true;
                vm.formula = '0';
            }

            // console.log(entryPoint);
            vm.total = `${vm.operator(
                parseFloat(vm.previous),
                parseFloat(vm.total)
            )}`;

            //此判斷用於,如果一值按1+2+2+2+,當按+時自動加總
            if(vm.isequal){
                vm.previous = null;
                vm.operatorClicked = true;
            }
            
        },
        keyCodes(key){
            const vm = this;
            switch (key.keyCode) {
                case 96:
                    return vm.append('0');
                    break;
                case 97:
                    return vm.append('1');
                    break;
                case 98:
                    return vm.append('2');
                    break;
                case 99:
                    return vm.append('3');
                    break;
                case 100:
                    return vm.append('4');
                    break;
                case 101:
                    return vm.append('5');
                    break;
                case 102:
                    return vm.append('6');
                    break;
                case 103:
                    return vm.append('7');
                    break;
                case 104:
                    return vm.append('8');
                    break;
                case 105:
                    return vm.append('9');
                    break;
                case 111:
                    return vm.divide();
                    break;
                case 106:
                    return vm.times();
                    break;
                case 107:
                    return vm.add();
                    break;
                case 109:
                    return vm.minus();
                    break;
                case 110:
                    return vm.dot('.');
                    break;
                case 8:
                    return vm.back();
                    break;
                case 13:
                    return vm.equal(true);
                    break;
                default:
                    break;
            }
        },
        clearAll(){
            const vm = this;
            vm.formula = '0';
            vm.total = '0';
            vm.operatorClicked = true;
            vm.operator = null;
            vm.previous = null;
            vm.isfirst = true;
            vm.isequal = true;
        }
    },
}
</script>


