<template>
    <div>
        <div class="panel d-flex justify-content-center align-items-center">
            <div class="clock"></div>
            <div class="hourHand" :style="{ transform: 'rotate(' + hourDeg + 'deg)' }"></div>
            <div class="minuteHand" :style="{ transform: 'rotate(' + minutesDeg + 'deg)' }"></div>
            <div class="secondHand" 
            :style="{ transform: `rotate(${secondsDeg}deg)`,  transition: `all ${animationTime}s cubic-bezier(0.1, 2.7, 0.58, 1)` }"></div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    @import '../../assets/scss/clock.scss';
</style>

<script>
export default {
    data(){
        return{
            hourDeg: 270,
            minutesDeg: 180,
            secondsDeg: 180,
            animationTime: 0.05,
        }
    },
    methods:{
        getTime(){
            const vm = this;
            const nowDate = new Date();
            const nowHour = nowDate.getHours();
            const nowMinutes = nowDate.getMinutes();
            const nowSeconds = nowDate.getSeconds();

            if(nowSeconds === 0){
                vm.animationTime = 0;
            }else{
                vm.animationTime = 0.05;
            }

            vm.hourDeg = (360 / 12) * nowHour + (24 * nowMinutes / 60) + 270;
            vm.minutesDeg = (360 / 60) * nowMinutes + 180;
            vm.secondsDeg = (360 / 60) * nowSeconds + 180;
        }
    },
    mounted(){
        setInterval(() => {
            this.getTime();
        }, 1000);
            

    }
}
</script>


