// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import '../node_modules/bootstrap/dist/css/bootstrap.css'  //css要在route之前載入,不然編譯後會跑版
import axios from 'axios' //主要的ajax套件 
import VueAxios from 'vue-axios' //將他轉為vue的套件 
import App from './App'
import router from './router'

Vue.use(VueAxios, axios) 


import '../node_modules/jquery/src/jquery'
import '../node_modules/popper.js'
import '../node_modules/bootstrap/dist/js/bootstrap'
// import 'bootstrap'

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
Vue.component('Loading', Loading);   //全域載入此套件

import currency from './router/filter/currency';
Vue.filter('currency', currency);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
})



