<template>
    <div>
        <div class="container">
            <div class="jumbotron" :style="{backgroundImage: 'url(' + jumbotronImg +')' }"></div>
            <div class="row">
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/nice.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">1F - 9x9 乘法表</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/nine_nine">前往</router-link>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/clock.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">2F - 時鐘</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/clock">前往</router-link>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/calc.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">3F - 計算機</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/calc">前往</router-link>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/worldClock.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">4F - 時區</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/world_clock">前往</router-link>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/airQuality.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">5F - 全台空氣指標儀表板</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/air_quality">前往</router-link>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-4 mt-3 mb-3">
                    <div class="card h-100 mx-auto" style="width: 18rem;">
                    <img src="../assets/img/sixtySeconds.png" class="card-img-top" alt="...">
                        <div class="card-body d-flex align-items-start flex-column">
                            <h5 class="card-title text-success h3">6F - 60秒算數遊戲</h5>
                            <router-link class="btn btn-outline-primary mt-auto" to="/sixty_seconds">前往</router-link>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<style lang="scss" scoped>
$animation-time: all 1s ease-out;

.jumbotron{
    height: 328px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.card{
    box-shadow: 0px 3px 10px grey;
    transition: $animation-time;
    overflow:hidden;
    &:hover{
        box-shadow: 0px 3px 10px rgb(34, 33, 33);
    }
    img{
        transform:scale(1,1);
        transition: $animation-time;
        &:hover{
            transform:scale(1.2,1.2);
        }
    }
    .btn{
        transition: $animation-time;
    }
    
    
}
</style>

<script>
export default {
    data(){
        return{
            jumbotronImg: require('../assets/img/hex.jpg'),
        }
    }
}
</script>


