<template>
    <div>
        <div class="panel d-flex justify-content-center ">
            <!-- start page -->
            <div class="d-flex flex-column flex-wrap justify-content-center align-items-center" v-if="startPage">
                <div class="start d-flex justify-content-around ">
                    <div class="num d-flex align-items-center">
                        60
                    </div>
                    <div class="fontDisplay d-flex flex-wrap  justify-content-around ">
                        <div class="upperRow d-flex  align-items-end ">
                            SECONDS
                            <div class="operator">
                                +−×÷
                                <div class="line"></div>
                                <div class="line"></div>
                            </div>
                        </div>
                        <div class="lowerRow ">
                            CHALLENGE
                        </div>
                    </div>
                </div>
                <div class="startBtn d-flex justify-content-center" @click="startBtn()">
                    START!
                </div>
                <div class="promptFont d-flex justify-content-center">
                    try to answer more as you can
                </div>
            </div>

            <!-- main page -->
            <div class="d-flex flex-column justify-content-center align-items-center" v-if="mainPage">
                <div class="main d-flex justify-content-between">
                    <div class="leftBlock d-flex flex-wrap">
                        <div class="sixtyFont ">
                            60 SECONDS CHALLENGE
                        </div>
                        <div class="score">
                            score
                        </div>
                        <div class="scoring ">
                            {{score}}
                        </div>
                    </div>

                    <div class="rightTimes">
                        {{minutes}} : {{second}}
                    </div>
                </div>
                <div class="calculation d-flex flex-wrap  justify-content-between">
                    <div class="topic">
                        {{randomOne}} <span>{{operator}}</span> {{randomTwo}} =
                    </div>
                    <input type="text" class="answer" v-model="inputValue" @keyup.enter="newtopic">          
                </div>
                    <div class="enterFont ml-auto">
                        press enter to answer
                    </div>
            </div>

            <!-- reset -->
            <div class="d-flex  justify-content-center" v-if="resetPage">
                <div class="reset d-flex flex-column  justify-content-center align-items-center">
                    <div class="sixtyFont ">
                        60 SECONDS CHALLENGE
                    </div>
                    <div class="final">
                        <span>—</span> YOUR FINAL SCORE <span>—</span>
                    </div>
                    <div class="score">
                        {{score}}
                    </div>
                    <div class="againBtn" @click="startBtn()">
                        TRY AGAIN!
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<style lang="scss" scoped>
// @import '../../assets/css/reset.css';
@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:300i,400i,700,700i&display=swap');
@import '../../assets/scss/sixtySeconds.scss';
</style>

<script>
export default {
    data(){
        return{
            startPage: true,
            mainPage: false,
            resetPage: false,

            // countdown
            minutes: '01',
            second: '00',

            //分數顯示
            score: '',
            //輸入的值
            inputValue: 0,
            answer: '',
            randomOne: '',
            randomTwo: '',
            operator: '',  //運算符
            firstNoScoring: false,
            addNum: 1,
        }
    },
    methods:{
        startBtn(){
            const vm = this;
            vm.startPage = false;
            vm.mainPage = true;
            vm.resetPage = false,
            vm.minutes = '01',
            vm.second = '00';
            vm.score = '000';
            vm.addNum = 1;
            vm.countDowntTimer();
            vm.newtopic();
        },
        newtopic(){
            const vm = this;
            
            if( parseInt(vm.second) === 0  ){
                vm.addNum = 1;
            }else if(parseInt(vm.second) <= 20){
                vm.addNum = 5;
            }
            vm.calculation(vm.addNum);
            vm.inputValue = '';
            if(parseInt(vm.second) <= 60  &&  parseInt(vm.second) >= 41){
                vm.randomOne = vm.randomNum(10);
                vm.randomTwo = vm.randomNum(10);
            }else if(parseInt(vm.second) <= 40 && parseInt(vm.second) >= 21){
                vm.randomOne =  vm.randomNum(100);
                vm.randomTwo =  vm.randomNum(100);
            }else if(parseInt(vm.second) <= 20 && parseInt(vm.second) >= 1){
                vm.randomOne =  vm.randomNum(1000);
                vm.randomTwo =  vm.randomNum(1000);
            }else{
                vm.randomOne =  vm.randomNum(10);
                vm.randomTwo =  vm.randomNum(10);
            } 

            const operatorArr = ['+', '-' , 'x', '÷'];
            const random = Math.floor(Math.random()*4);
            vm.operator = operatorArr[random]; //+−×÷
        },
        calculation(num){
            const vm = this;
            if(!vm.firstNoScoring) {
                vm.firstNoScoring = true; 
                return;
            }
            
            switch (vm.operator) {
                case '+':
                    vm.answer = parseFloat(vm.randomOne) + parseFloat(vm.randomTwo);
                    if(parseFloat(vm.answer) ===  parseFloat(vm.inputValue)){
                        vm.score = parseFloat(vm.score) + num;
                    } else if(parseFloat(vm.score) > 0){
                        vm.score = parseFloat(vm.score) - 1;
                    }
                    vm.scoreStr();
                    break;
                case '-':
                    vm.answer = parseFloat(vm.randomOne) - parseFloat(vm.randomTwo);
                    if(parseFloat(vm.answer) ===  parseFloat(vm.inputValue)){
                        vm.score = parseFloat(vm.score) + num;
                    } else if(parseFloat(vm.score) > 0){
                        vm.score = parseFloat(vm.score) - 1;
                    }
                    vm.scoreStr();
                    break;
                case 'x':
                    vm.answer = parseFloat(vm.randomOne) * parseFloat(vm.randomTwo);
                    if(parseFloat(vm.answer) ===  parseFloat(vm.inputValue)){
                        vm.score = parseFloat(vm.score) + num;
                    } else if(parseFloat(vm.score) > 0){
                        vm.score = parseFloat(vm.score) - 1;
                    }
                    vm.scoreStr();
                    break;
                case '÷':
                    vm.answer = parseFloat(vm.randomOne) / parseFloat(vm.randomTwo);
                    if(parseFloat(vm.answer) ===  parseFloat(vm.inputValue)){
                        vm.score = parseFloat(vm.score) + num;
                    } else if(parseFloat(vm.score) > 0){
                        vm.score = parseFloat(vm.score) - 1;
                    }
                    vm.scoreStr();
                    break;
                default:
                    break;
            }
        },
        scoreStr(){

            const vm = this;
            if(parseInt(vm.score) <= 9 && parseInt(vm.score) >= 0){
                vm.score = "0"+"0"+ parseInt(vm.score);
            }else if(parseInt(vm.score) < 99){
                vm.score = "0" + parseInt(vm.score);
            }else{
                vm.score = parseInt(vm.score);
            }
        },
        randomNum(num){
            return Math.floor(Math.random()*num);
        },
        countDowntTimer(){
            const vm = this;  
            let countdown = setInterval(() => {
                if(parseInt(vm.second) === 0){
                    vm.minutes = parseInt(vm.minutes - 1);
                    vm.second = '60';
                    vm.minutes = vm.minutes < 10 ? "0" + vm.minutes : vm.minutes;
                }
                vm.second--;
                vm.second = parseInt(vm.second) < 10 ? '0' +  vm.second : vm.second;
                if(vm.second === '00' && vm.minutes === '00'){
                    vm.mainPage = false;
                    vm.resetPage = true;
                    vm.score = parseInt(vm.score);
                    clearInterval(countdown);
                    return;
                }
            }, 1000);
        },
    }
}
</script>



