<template>
    <div>
        <loading :active.sync="pageLoading"></loading>
        <div class="panel">
            <div class="container">
                <div class="content p-3">
                    <div class="row m-0">
                        <div class="col-lg-5 col-12 p-0">
                            <div class="titleGroup d-flex justify-content-center">
                                <div class="title d-flex align-items-center">空氣品質指標</div>
                                <div class="titleEn d-flex align-items-center">(AQI)</div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <div class="citySelector">
                                     <select class="select" name="" id="" @change="filterSelectCity($event)">
                                        <option value="基隆市" disabled>請選擇地區</option>
                                        <option :value="item.County" v-for="item in dropDownCity" :key="item.Latitude">{{item.County}}</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-7 col-12 d-flex justify-content-center p-0">
                            <div class="ststusDisplay good">
                                <div class="range d-flex justify-content-center align-items-center">0~50</div>
                                <div class="font d-flex justify-content-center align-items-center">良好</div>
                            </div>
                            <div class="ststusDisplay ordinary">
                                <div class="range d-flex justify-content-center align-items-center">51～100</div>
                                <div class="font d-flex justify-content-center align-items-center">普通</div>
                            </div>
                             <div class="ststusDisplay sensitive">
                                <div class="range d-flex justify-content-center align-items-center">101～150</div>
                                <div class="font d-flex justify-content-center align-items-center">對敏感族群 不健康</div>
                            </div>
                             <div class="ststusDisplay unhealthy">
                                <div class="range d-flex justify-content-center align-items-center">151～200</div>
                                <div class="font d-flex justify-content-center align-items-center">對所有族群 不健康</div>
                            </div>
                             <div class="ststusDisplay veryUnhealthy">
                                <div class="range d-flex justify-content-center align-items-center">201～300</div>
                                <div class="font d-flex justify-content-center align-items-center">非常不健康</div>
                            </div>
                             <div class="ststusDisplay harm">
                                <div class="range d-flex justify-content-center align-items-center">301～400</div>
                                <div class="font d-flex justify-content-center align-items-center">危害</div>
                            </div>
                        </div>
                        <div class="col-lg-12 d-none d-lg-block">
                            <div class="line d-flex justify-content-center ">
                                <div class="cityName d-flex align-items-center">
                                    {{cityTitle}}
                                </div>
                                <div class="point align-self-center"></div>
                                <div class="time align-self-center">{{parameter.PublishTime}} 更新</div>
                             </div>
                        </div>
                        <div class="col-lg-5 col-12 d-flex justify-content-center p-0 ">
                            <div class="currentlyZone sticky-top">
                                <div class="row m-0">
                                    <div class="col-6 zoneName d-flex justify-content-center align-items-center">
                                        {{parameter.SiteName}}
                                    </div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center maintain" v-if="parameter.AQI === ''">維護</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center good" v-else-if="parameter.AQI <= 50" >{{parameter.AQI}}</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center ordinary" v-else-if=" parameter.AQI >= 51 && parameter.AQI <= 100">{{parameter.AQI}}</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center sensitive" v-else-if=" parameter.AQI >= 101 && parameter.AQI <= 150">{{parameter.AQI}}</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center unhealthy" v-else-if=" parameter.AQI >= 151 && parameter.AQI <= 200">{{parameter.AQI}}</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center veryUnhealthy" v-else-if=" parameter.AQI >= 201 && parameter.AQI <= 300">{{parameter.AQI}}</div>
                                    <div class="col-6 zoneNum d-flex justify-content-center align-items-center harm" v-else>{{parameter.AQI}}</div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">臭氧</div>
                                                <div class="unit ml-2">O3 (ppb)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter.O3}}</div>
                                        </div>
                                    </div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">懸浮微粒</div>
                                                <div class="unit ml-2">PM10 (μg/m³)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter.PM10}}</div>
                                        </div>
                                    </div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">細懸浮微粒</div>
                                                <div class="unit ml-2">PM2.5 (μg/m³)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter['PM2.5']}}</div>
                                        </div>
                                    </div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">一氧化碳</div>
                                                <div class="unit ml-2">CO (ppm)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter.CO}}</div>
                                        </div>
                                    </div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">二氧化硫</div>
                                                <div class="unit ml-2">SO2 (ppb)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter.SO2}}</div>
                                        </div>
                                    </div>
                                    <div class="px-4 mx-auto">
                                        <div class="parameterGroup noTwo d-flex">
                                            <div class="d-flex align-items-center">   
                                                <div class="title">二氧化氮</div>
                                                <div class="unit ml-2">NO2 (ppb)</div>
                                            </div>
                                            <div class="value d-flex align-items-center ml-auto">{{parameter.NO2}}</div>
                                        </div>
                                    </div> 
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-7 col-12">
                            <div class="row">
                                <div class="col-6" v-for="item in selectCity" :key="item.Latitud">
                                    <div class="otherZone ">
                                        <div class="row m-0">
                                            <div class="col-6 zoneName d-flex justify-content-center align-items-center p-0" @click="getParameter(item)">
                                                {{item.SiteName}}
                                            </div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center maintain" v-if="item.AQI === ''">維護</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center good" v-else-if="item.AQI <= 50">{{item.AQI}}</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center ordinary" v-else-if=" item.AQI >= 51 && item.AQI <= 100">{{item.AQI}}</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center sensitive" v-else-if=" item.AQI >= 101 && item.AQI <= 150">{{item.AQI}}</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center unhealthy" v-else-if=" item.AQI >= 151 && item.AQI <= 200">{{item.AQI}}</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center veryUnhealthy" v-else-if=" item.AQI >= 201 && item.AQI <= 300">{{item.AQI}}</div>
                                            <div class="col-6 zoneNum d-flex justify-content-center align-items-center harm" v-else>{{item.AQI}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="footer px-5">
                <div class="container d-flex ">
                    <div class="source d-flex align-items-center mr-auto">資料來源：行政院環境保護署</div>
                    <div class="copyright d-flex align-items-center">Copyright © 2019 HexSchool. All rights reserved.</div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import '../../assets/scss/airQuality.scss';
</style>


<script>
export default {
    data(){
        return{
            cityTitle: '基隆市',
            allZone: [],
            dropDownCity: [],
            selectCity:[],
            firstLoad: true,
            parameter: '',
            pageLoading: false,
        }
    },
    methods:{
        filterDropDownCity(){
            const vm = this;
            const set = new Set();
            vm.dropDownCity = vm.allZone.filter(item => !set.has(item.County) ? set.add(item.County) : false);
        },
        filterSelectCity(city){
            const vm = this;
            if(!vm.firstLoad){
                city = city.target.value;
            }
            vm.cityTitle = city; 
        
            vm.selectCity = vm.allZone.filter(function(item){
                return item.County === city;
            });
            vm.parameter = vm.selectCity[0]; //預設參數細項
            
            vm.pageLoading = false;
            vm.firstLoad = false;
        },
        getParameter(item){
            const vm = this;
            console.log(item);
            vm.parameter = item;
        }
    },
    mounted(){
        const vm = this;
        const api = 'https://script.google.com/macros/s/AKfycbzl6KKgb4v2-F3SCVxVaXjnMwM_XQvnk2A08nw7NjmGfuRVmak0/exec?url=http://opendata2.epa.gov.tw/AQI.json';
        vm.pageLoading = true;
        vm.$http.get(api).then((response) => {
            vm.allZone = response.data;
            vm.filterDropDownCity();
            vm.filterSelectCity('基隆市');
        })
    }
}
</script>



