<template>
    <div>
        <div class="panel mx-auto">
            <div class="content mx-auto">
                <div class="head">
                    <div class="topLine"></div>
                    <div class="title text-center">WORLD CLOCK</div>
                    <div class="bottomLine"></div>
                </div>
                
                <div class="timeZone">
                    <div v-for="item in zone" :key="item.id" 
                    :class="['zoneItem',{'london': item.class === 'london'},{'bangkok': item.class === 'bangkok'},{'taiwan': item.class === 'taiwan'} ]">
                        <div class="row m-0">
                            <div class="col-6 p-0">
                                <div class="zoneName">{{item.name}}</div>
                                <div class="date">{{item.day[0]}} {{item.monthName}}. {{item.year[0]}}</div>
                            </div>
                            <div class="col-6 p-0">
                                <div class="time ml-auto">{{item.time}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import '../../assets/scss/worldClock.scss';
</style>

<script>
export default {
    data(){
        return{
            zone:[],
        }
    },
    methods:{
        getWorldTimes(){
            const vm = this;
            const classNames = ['newYork', 'london', 'bangkok', 'taiwan', 'sydney'];
            const zoneNames = ['NEW YORK', 'LONDON', 'BANGKOK', 'TAIWAN', 'SYDNEY'];
            const monthNames = ["January", "February", "March", "April", "May", "June","July", "August", "September", "October", "November", "December"];
            let NEWYORK_Time = new Date().toLocaleString('en-us', {timeZone: 'America/New_York', hour12: false});
            let LONDON_Time = new Date().toLocaleString('en-us', {timeZone: 'Europe/London', hour12: false});
            let BANGKOK_Time = new Date().toLocaleString('en-us', {timeZone: 'Asia/Bangkok', hour12: false});
            let TAIWAN_Time = new Date().toLocaleString('en-us', {timeZone: 'Asia/Taipei', hour12: false});
            let SYDNEY_Time = new Date().toLocaleString('en-us', {timeZone: 'Australia/Sydney', hour12: false});
            const timeArry = [NEWYORK_Time, LONDON_Time, BANGKOK_Time, TAIWAN_Time, SYDNEY_Time];
            let NowMonth = new Date();
            let index = 0;
            timeArry.forEach(items => {
                let getItem = items.split(',');
                let FullDate = getItem.slice(0,1) + '';  //日期
                let Month_Name = monthNames[NowMonth.getMonth()];  //月份英文字體
                let year_day = FullDate.split('/').slice(1);  //年 ， 日
                let FullTime = getItem.slice(1) + '';
                vm.zone[index] = ({
                    name: zoneNames[index],
                    id: index,
                    class: classNames[index],
                    day: year_day.slice(0,1),
                    year: year_day.slice(1),
                    monthName: Month_Name,
                    time: FullTime.split(':').slice(0,2).join(':'),
                }) 
                index++;
            });
        }
    },
    created(){
        this.getWorldTimes();
        setInterval(() => {
            this.getWorldTimes();
            this.$forceUpdate()
        }, 1000);
    }
}
</script>






