import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import nineNine from '@/components/view/nineNineMultiplicationTable'  //F1
import clock from '@/components/view/clock'  //F2
import calc from '@/components/view/calc'    //F3
import worldClock from '@/components/view/worldClock'    //F4
import airQuality from '@/components/view/airQuality'    //F5
import sixtySeconds from '@/components/view/sixtySeconds'    //F6


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/nine_nine',
      name: 'nineNine',
      component: nineNine
    },
    {
      path: '/clock',
      name: 'clock',
      component: clock
    },
    {
      path: '/calc',
      name: 'calc',
      component: calc
    },
    {
      path: '/world_clock',
      name: 'worldClock',
      component: worldClock
    },
    {
      path: '/air_quality',
      name: 'airQuality',
      component: airQuality
    },
    {
      path: '/sixty_seconds',
      name: 'sixtySeconds',
      component: sixtySeconds
    }
  ]
})



